import React, { useEffect, useState } from 'react';
import firebase from '../Firestore/Firestore';
import "firebase/firestore";

import './Portfolio.css';
import border_blue from '../../../img/border_blue.svg';

const Portfolio = () => {

    const [projects,setProjects] = useState([]);
    const [index,setIndex] = useState(0);

    useEffect(()=>{
        var db = firebase.firestore();
        db.collection("projects").get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
                setProjects(projects => [...projects, doc.data()]);
            });
        });
    },[])

    const navigatePortfolio = (direction) => {
        if (direction==='left') setIndex(index<=0?0:index-1);
        if (direction==='right') setIndex(index+1>=projects.length-1?projects.length-1:index+1);
    }

    const handleKeyboard = e => {
        if(e.keyCode===37) navigatePortfolio('left')
        if(e.keyCode===39) navigatePortfolio('right')
    }
    document.addEventListener("keydown",handleKeyboard);

    const renderProjects = (projects) => {
        const e = projects[index];
        if(e){
            return (
                <div key={e.name} className="project">
                    <div className="project-image">
                        <a href={e.url} target="_blank" rel="noreferrer" title={e.name}>
                            <img src={e.image} alt={e.name}/>
                        </a>
                    </div>
                    <div className="project-info">
                        <a href={e.url} target="_blank" rel="noreferrer" title={e.name}>
                            <h2>{e.name}</h2>
                        </a>
                        <p>{e.description}</p>
                        <a href={e.url} target="_blank" className="ui blue button" rel="noreferrer" title={e.name} style={{borderImageSource: `url(${border_blue})`}}>
                            Go to website
                        </a>
                    </div>
                </div>
            )
        }else{
            return (
                <div className="project">
                    <div className="project-info">
                        <i className="spinner icon"></i>
                    </div>
                </div>
            )
        }
    }

    return (
        <div id="portfolio">
            <h1>Portfolio</h1>
            <p>Use the arrows to navigate between projects:</p>
            <div className="projects">
                <div className="portfolio-nav left" onClick={()=>navigatePortfolio('left')}>
                    <i className="arrow alternate circle left icon"></i>
                </div>
                {renderProjects(projects)}
                <div className="portfolio-nav right" onClick={()=>navigatePortfolio('right')}>
                    <i className="arrow alternate circle right icon"></i>
                </div>
            </div>
        </div>
    )
}

export default Portfolio;